.sidebar {
    background: $color-1;
    font-family: $color-2;
    height: 100%;
    min-height: $minHeight;
    width: 264px;
    border-radius: $borderRadius;
    -webkit-border-radius: $borderRadius;
    -moz-border-radius: $borderRadius;
    -ms-border-radius: $borderRadius;
    -o-border-radius: $borderRadius;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 250px 1fr 108px;
    grid-template-areas: 
        "1"
        "2"
        "3";

    .id {
        grid-area: 1;
        text-align: center;
        user-select: none;
        position: relative;

        .idContent {
            position: relative;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            -webkit-transform: translateX(-50%);
            -moz-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
            -o-transform: translateX(-50%);
            width: 100%;
            margin-top: 1.5rem;

            img {
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                box-shadow: 0 0 0 4px $color-3, 0 0 0 8px $color-4;
                width: 50%;
            }

            h3 {
                font-size: 1.3rem;
                text-transform: uppercase;
                letter-spacing: .2rem;
                margin-top: 1.5rem;
                color: $color-5;
            }
        }
    }
    .navigation {
        grid-area: 2;
        padding: 2rem 1rem;

        ul {
            height: 100%;
            display: grid;

            li {
                padding: 1rem 0 1rem .4rem;
                font-size: 1.3rem;
                position: relative;

                &:hover i {
                    transform: translateX(-20%);
                    -webkit-transform: translateX(-20%);
                    -moz-transform: translateX(-20%);
                    -ms-transform: translateX(-20%);
                    -o-transform: translateX(-20%);
                }

                a {
                    color: $color-5;
                }

                .navActive {
                    color: $color-4;
                }

                i {
                    position: absolute;
                    font-size: 0.95rem;
                    transform: translateY(3px);
                    -webkit-transform: translateY(3px);
                    -moz-transform: translateY(3px);
                    -ms-transform: translateY(3px);
                    -o-transform: translateY(3px);
                    transition: .2s ease;
                    -webkit-transition: .2s ease;
                    -moz-transition: .2s ease;
                    -ms-transition: .2s ease;
                    -o-transition: .2s ease;
                }

                span {
                    margin-left: 2.7rem
                }
            }
        }
    }
}

.socialNetwork {
    grid-area: 3;

    ul {
        display: flex;
        width: 250px;
        padding: 0 14px;
        justify-content: space-between;

        li {
            font-size: 1.5rem;
            background: $color-5;
            color: $color-1;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
            padding: .5rem .8rem;
            transition: 0.3s;
            -webkit-transition: 0.3s;
            -moz-transition: 0.3s;
            -ms-transition: 0.3s;
            -o-transition: 0.3s;
            cursor: pointer;

            a:visited {
                color: $color-1;
            }
            &:nth-child(1){
                padding: .5rem .85rem;
            }
            &:nth-child(4){
                padding: .5rem .75rem;
            }
            &:hover {
                background: $color-4;
            }
        }
    }
    .signature {
        margin-top: 2rem;
        text-align: center;
        letter-spacing: .1rem;
        font-size: .7rem;
    
        p {
            color: $color-5;
            
            &:hover {
                color: $color-4;
            }
        }
    }
}

@media screen and (max-width: 850px) {
    .sidebar {
        display: flex;
        min-height: 50px;
        margin-bottom: .2rem;
        width: 100%;

        .id, .socialNetwork {
            display: none;
        }
        .navigation {
            width: 100%;
            padding: .1rem 1rem;

            ul {
                display: flex;
                justify-content: space-between;

                span {
                    margin-left: 1.5rem !important;
                }
            }
        }
    }
}

@media screen and (max-width: 580px){
    .sidebar {
        ul {
            justify-content: space-around !important;

            li {
                padding: 1rem 0 !important;
                justify-content: center;
            }

            i {
                font-size: 1.4rem !important;
                transform: translateY(-3px) !important;
                -webkit-transform: translateY(-3px) !important;
                -moz-transform: translateY(-3px) !important;
                -ms-transform: translateY(-3px) !important;
                -o-transform: translateY(-3px) !important;
}
            span {
                display: none;
            }
        }
    }
}
